import { isArray } from "lodash";

export const NTNX_API_KEY_HEADER = 'X-ntnx-api-key';

type Options = {
  value: any;
  api: string;
  field: string;
  mapper?: Function;
  filter?: Function;
  initial?: string;
  name?: string;
}
/**
 * Helper class for dealing with the nutanix API
 */
export class Nutanix {
  public endpoint: string = '';
  public username: string = '';
  public password: string = '';
  public port: string = '';
  public insecure: boolean = false;
  public clusterReferenceId: string = '';
  private credentialID: string = '';
  private endpoints: any;

  private $dispatch: any;

  constructor($store: any, obj: any) {
    if (obj.nutanixcredentialConfig) {
      Object.keys(obj.nutanixcredentialConfig).forEach((key) => {
        (this as any)[key] = obj.nutanixcredentialConfig[key];
      });
      this.credentialID = obj.id;
    } else {
      // Copy from options to this
      Object.keys(obj).forEach((key) => {
        (this as any)[key] = obj[key];
      });
    }

    this.$dispatch = $store.dispatch;
  }

  public async testConnection() {
    const baseUrl = `/meta/proxy/${this.endpoint}:${this.port}`;
    const url = `${baseUrl}/api/clustermgmt/v4.1/config/clusters`;
    const headers: any = {
      Accept: 'application/json'
    };

    if (this.username === NTNX_API_KEY_HEADER) {
      headers[NTNX_API_KEY_HEADER] = this.password;
    } else {
      headers['X-API-Auth-Header'] = 'Basic ' + btoa(this.username + ':' + this.password);
    }

    try {
      const res = await this.$dispatch('management/request', {
        url,
        headers,
        method: 'GET',
        redirectUnauthorized: false,
      }, { root: true });

      if (res._status === 502) {
        return { error: 'Could not proxy request - URL may not be in Rancher\'s allow list' };
      }

      return res;

    } catch (e) {
      console.error(e); // eslint-disable-line no-console
      return { error: e };
    }
  }

  public async getClusterList(value: any, initial?: string) {
    return await this.getOptions({
      value,
      api: "/api/clustermgmt/v4.1/config/clusters",
      field: 'data',
      filter: (cluster: any) => cluster.config.hypervisorTypes.includes("AHV"),
      initial
    });
  }

  public async getImages(value: any, initial?: string) {
    return await this.getOptions({
      value,
      api: '/api/vmm/v4.1/content/images',
      field: 'data',
      initial
    });
  }

  public async getNetwork(value: any, initial?: string) {
    // Fetch all VPCs once and build a lookup table
    const vpcMap = await this.buildVpcMap();

    return await this.getOptions({
      value,
      api: '/api/networking/v4.1/config/subnets',
      field: 'data',
      mapper: async (network: any) => {
        const vpc = network.subnetType === "OVERLAY" ? vpcMap.get(network.vpcReference) : undefined;
        return {
          ...network,
          baseName: network.name,
          name: network.subnetType === "OVERLAY" ? `${network.name} (${vpc?.name || 'Unknown VPC'})` : network.name,
        }
      },
      filter: (network: any) =>
        (network.subnetType == "OVERLAY" ||
          network.clusterReference == this.clusterReferenceId ||
          network?.clusterReferenceList.includes(this.clusterReferenceId)) && !network.isExternal,
      initial
    });
  }

  private async buildVpcMap(): Promise<Map<string, any>> {
    const vpcMap = new Map<string, any>();
    const vpcOptions: any = {
      busy: false,
      enabled: false,
      selected: '',
      options: []
    };

    try {
      await this.getOptions({
        value: vpcOptions,
        api: '/api/networking/v4.1/config/vpcs',
        field: 'data'
      });

      vpcOptions.options.forEach((option: any) => {
        const vpc = option?.value;

        if (vpc?.extId) {
          vpcMap.set(vpc.extId, vpc);
        }
      });
    } catch (e) {
      console.error('Error fetching VPCs:', e); // eslint-disable-line no-console
    }

    return vpcMap;
  }

  public async getStorageContainer(value: any, initial?: string) {
    return await this.getOptions({
      value,
      api: '/api/clustermgmt/v4.1/config/storage-containers',
      field: 'data',
      filter: (storage: any) => storage.clusterExtId === this.clusterReferenceId,
      initial
    });
  }

  public async getCategories(value: any, initial?: string) {
    return await this.getOptions({
      value,
      api: '/api/prism/v4.1/config/categories',
      field: 'data',
      mapper: (categorie: any) => { return { ...categorie, name: `${categorie.key}=${categorie.value}` } },
      filter: (categorie: any) => categorie.key !== "Project",
      initial
    });
  }

  public async getProjectsName(value: any, initial?: string) {
    return await this.getOptions({
      value,
      api: '/api/nutanix/v3/projects/list',
      field: 'entities',
      mapper: (project: any) => { return { ...project, name: `${project.spec.name}` } },
      initial
    });
  }

  // public async getNetworkNames(value: any, initial?: string) {
  //   return await this.getOptions(value, '/os-tenant-networks', 'networks', (network: any) => {
  //     return {
  //       ...network,
  //       name: network.label
  //     };
  //   }, initial);
  // }

  private async constructTotalResponse(apiPath: string, initialDataLength: number, total: number) {
    const pageCount = Math.ceil(total / initialDataLength);
    const data = [];
    for (let i = 1; i < pageCount; i++) {
      const nextPageResponse = await this.makeComputeRequest(this.withPage(apiPath, i));
      data.push(...nextPageResponse.data);
    }

    return data;
  }

  private async constructProjectTotalResponse(apiPath: string, initialDataLength: number, total: number) {
    const pageCount = Math.ceil(total / initialDataLength);
    const entities = [];
    for (let i = 1; i < pageCount; i++) {
      const nextPageResponse = await this.makeComputeRequest(this.withPage(apiPath, i), 'POST');
      entities.push(...nextPageResponse.entities);
    }

    return entities;
  }

  public async getOptions(options: Options) {
    const { value, api, mapper, filter, initial, field } = options;
    // We are fetching the data for the options
    value.busy = true;
    value.enabled = true;
    value.selected = isArray(value.selected) ? [] : '';

    let res;

    if (api === '/api/nutanix/v3/projects/list') {
      res = await this.makeComputeRequest(this.withPage(api, 0), 'POST');
      const total = res?.metadata?.total_matches ?? 0;
      const pageCount = res?.entities?.length ?? 0;
      if (pageCount < total) {
        const entities = await this.constructProjectTotalResponse(api, pageCount, total);
        res.entities = [...res.entities, ...entities];
      }
    }
    else {
      res = await this.makeComputeRequest(this.withPage(api, 0));
      const total = res?.metadata?.totalAvailableResults ?? 0;
      const pageCount = res?.data?.length ?? 0;

      if (pageCount < total) {
        const data = await this.constructTotalResponse(api, pageCount, total);
        res.data = [...res.data, ...data];
      }
    }

    if (res && (res as any)[field]) {
      let list = (res as any)[field] || [];

      if (filter) {
        list = list.filter((k: any) => filter(k));
      }

      if (mapper) {
        list = await Promise.all(list.map(async (k: any) => await mapper(k)));
      }

      value.options.forEach((selected: any) => { list.push(selected) });

      value.options = this.convertToOptions(list);
      value.busy = false;

      if (value.options.length < list.length) {
        const seenExtIds = new Set();
        const unique = list.filter((obj: any) => {
          if (!obj?.extId) {
            return false;
          }

          if (seenExtIds.has(obj.extId)) {
            return true;
          }

          seenExtIds.add(obj.extId);

          return false;
        });
        value.duplicates = unique;
      }

      if (initial) {
        const found = value.options.find((option: any) => option.value.name === initial);

        if (found) {
          value.selected = found.value;
        }
      }

    } else {
      value.options = [];
      value.selected = isArray(value.selected) ? [] : null;
      value.busy = false;
      value.enabled = false;
    }
  }

  public async makeComputeRequest(api: string, method: string = 'GET') {
    const baseUrl = `/meta/proxy/${this.endpoint}:${this.port}`;
    const url = `${baseUrl}${api}`;

    const headers: any = {
      Accept: 'application/json',
      "Content-Type": 'application/json'
    };

    if (this.username === NTNX_API_KEY_HEADER) {
      headers[NTNX_API_KEY_HEADER] = this.password;
    } else {
      headers['X-API-Auth-Header'] = 'Basic ' + btoa(this.username + ':' + this.password);
    }

    try {
      const res = await this.$dispatch('management/request', {
        url,
        headers,
        method: method,
        data: JSON.stringify({}),
        redirectUnauthorized: false,
      }, { root: true });

      return res;
    } catch (e) {
      console.error(e); // eslint-disable-line no-console
    }
  }


  private convertToOptions(list: any) {
    const seenExtIds = new Set();
    const unique = list.filter((obj: any) => {
      if (!obj?.extId) {
        return true;
      }

      if (seenExtIds.has(obj.extId)) {
        return false;
      }

      seenExtIds.add(obj.extId);

      return true;
    });

    const sorted = (unique || []).sort((a: any, b: any) => {
      const nameA = a?.name ?? '';
      const nameB = b?.name ?? '';
      return nameA.localeCompare(nameB);
    });
    const nameCounts = sorted.reduce((counts: Map<string, number>, item: any) => {
      const key = item?.name;

      if (key) {
        counts.set(key, (counts.get(key) || 0) + 1);
      }

      return counts;
    }, new Map<string, number>());

    return sorted.map((p: any) => {
      const hasDuplicateName = !!p?.name && (nameCounts.get(p.name) || 0) > 1;

      return {
        label: hasDuplicateName && p?.extId ? `${p.name} (${p.extId})` : p.name,
        value: p
      };
    });
  }

  private withPage(apiPath: string, page: number): string {
    const separator = apiPath.includes('?') ? '&' : '?';

    return `${apiPath}${separator}$page=${page}`;
  }
}
